@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700,800);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Index Page */

html, body{
    height: 100%;
    width: 100%;
    font-family: "Open Sans";
    background: black;
    color: white;
}

body{
    margin: 0;
}

#index-header {
    height: 100vh;
    background-image: url(/static/media/header-background.c250bf68.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    color: black;
}

#index-header img{
    margin-top: 25vh;
}

#index-header h1 {
    text-transform: uppercase;
    font-weight: 400;
    letter-spacing: 0.3rem;
    margin-top: 4vh;
    text-align: center;
    font-family: "Open Sans";
    font-weight:    400;
}

#index-header button{
    height: 4rem;
    width: 16rem;
    margin-top: 1.5rem;
    border: 0px;
    background-color: white;
    text-transform: uppercase;
    font-family: "Courier";
    font-weight: 300;
    font-family: "Open Sans";
    letter-spacing: 0.2rem;
    padding: 1.5em 3em;
}

#index-header button:hover{
    font-weight: 800;
}

/* Home Page : Header */

#nav-header-and-skills-container{
 background-image: url(/static/media/body-navbar-background.710870f7.png);
 background-size: cover;
}

#nav-header-container{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

#nav-header-container img{
    margin-top: 3rem;
    margin-bottom: 1rem;
}

#nav-header-container nav{
    width: 50%;
    margin-bottom: 2rem;
}

#nav-header-container ul{
    list-style: none;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    padding: 0;
}

#nav-header-container li:hover{
    text-decoration: underline;
}

#nav-header a{
    text-decoration: none;
    color: white;
}

/* Home Page : My Skills */

#my-skills{
    background-image: url(data:image/png;base64,);
    background-size: cover;
    padding: 4rem 20rem;
}

#my-skills-div{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    width: 100%;
}

#my-skills-description{
    width: 20rem;
    margin: auto;
    text-align: start;
}

#my-skills-box-grid{
    width: 900px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    margin: auto;
    margin-top: 2rem;
}

#my-skills-box-grid div{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
}


#my-skills i {
    font-size: 3rem;
}

#my-skills-box-grid ul{
    list-style-type: none;
    padding: 0;
}

/* Home Page : My Projects */

#my-projects{
    background-image: url(/static/media/my-projects-bg.9c05370f.png);
    background-size: cover;
}

#my-projects-header{
    text-align: center;
}

#my-projects h2{
    margin: 0;
    margin-bottom: 2rem;
    padding-top: 5rem;
}

#my-projects p{
    font-weight: 300;
    margin: 0 25%;
    margin-bottom: 2rem;
}

#my-projects-grid{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 0 15rem;
    padding-bottom: 3rem;
}

#my-projects-grid > div{
    margin: 1.2rem 1rem;
}

/* Home Page : About Me */

#about-me{
    background-image: url(/static/media/about-me-bg.8aa5004a.png);
    background-size: cover;   
}

#about-me h2{
    margin-top: 5rem;
}

#about-me p{
    font-size: 3rem;
    text-transform: uppercase;
    letter-spacing: 0.2em;
}

#about-me a{
    color: white;
    margin: 0 2rem;
}

#about-me > div{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

#about-me button{
    width: 15rem;
    height: 4rem;
    margin-top: 3rem;
    margin-bottom: 5rem;
    text-transform: uppercase;
    font-weight: 300;
    padding: 1em 1em;
}

#about-me button:hover{
    font-weight: 800;
}

/* Home Page : Footer */

#footer{
    background: #090909;
}

#footer > div {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

#footer img{
    margin: 1em 0;
    width: 50px;
}

#footer p{
    margin: 0;
    margin-bottom: 0.5em;
}

@media only screen and (max-width: 1024px){
    #nav-header-container nav{
        width: 80%;
    }
    #my-skills{
        padding: 0;
    }
    #my-skills h2{
        padding-top: 3rem;
    }
    #my-skills-description {
        width: 80%;
    }
    #my-skills-box-grid {
        width: 30%;
    }

    #my-skills-box-grid div{
        width: 100%;
        margin-top: 2rem;
    }

    #my-skills-box-grid div:nth-child(1){
        margin-top: 0;
    }
}

@media only screen and (max-width: 800px) {
    #nav-header-container nav{
        width: 90%;
    }
    #my-skills-box-grid {
        width: 80%;
    }
}

@media only screen and (max-width: 500px){
    #my-projects div {
        width: 100%;
    }
    #my-projects-grid{
        margin: auto;
    }
    #my-projects img{
        width: 80%;
    }
    #about-me h2{
        margin-top: 2rem;
    }
    #about-me p{
        margin-top: 1rem;
    }
}

@media only screen and (max-width: 320px){
    body{
        font-size: 0.8rem;
    }
    #about-me a {
        margin: 0.5rem;
    }
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

